.timer-container {
    padding: 30px;
    position: relative;
}

.progress {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;

}

.inner-progress {
    background: #ca4c4c;
    height: 2px;
}

.timer{
    font-size: 60px;
    font-family: 'Share Tech Mono', monospace;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}



.tomato-container {
display: -ms-flexbox;
display: flex;
-ms-flex-pack: center;
    justify-content: center;
-ms-flex-direction: column;
    flex-direction: column;
}

.tomato {

    font-size: 13px;
    margin: 0px 4px;
}

@-webkit-keyframes blinker {
  20% { opacity: 0; }
}

@keyframes blinker {
  20% { opacity: 0; }
}


